body,
h1,
h2,
li,
ul,
button {
  margin: 0;
  padding: 0;
  font-family: 'Inter', sans-serif;
  list-style: none;
}

* {
  box-sizing: border-box;
}

.App {
  margin: auto;
  height: 100vh;
  max-width: 1440px;
}

@font-face {
  font-family: 'Inter';
  src: url('./fonts/Inter-Regular.ttf');
  font-weight: 400;
}

@font-face {
  font-family: 'Inter';
  src: url('./fonts/Inter-SemiBold.ttf');
  font-weight: 600;
}